.navigation ul
{
  
  display: flex;
  
}

.navigation ul li 
{
  
  list-style: none;
  width: 500px;
  height: 100px;
  color: black;
  font-size: 25px;
  
}



.navigation ul li a
{
  float: left;
  position:relative;
  /* display:inline-block; */
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: black;
  text-align: center;
  margin-left: 20px;
  
}

.navigation ul li a:hover
{
  position:relative;
  display:inline-block;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: rgb(25, 33, 138);
  text-align: center;
  
}


.navigation ul li .icon
{
  position: relative;
  line-height: 30px;
  transition: 0.5s;
  font-size: 40px;
  white-space: nowrap;
  
  
}

.navigation ul li .icon:hover
{
  vertical-align: top;

  line-height: 30px;
  transition: 0.5s;
  font-size: 70px;
  
}






